html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background: #1c92d2;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #f2fcfe, #1c92d2);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #f2fcfe, #1c92d2);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedorMedio {
  width: 95%;
  max-width: 80rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, .heading-blanco, h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .heading-blanco {
  font-size: 3.6rem;
  color: #5e311e;
}

h3 {
  font-size: 3.2rem;
}

.heading-blanco {
  color: #fff;
}
.heading-blanco span {
  color: #fff;
}
.heading-blanco::after {
  display: none;
}

[class$=__heading] {
  margin: 2rem 0;
}

.formulario {
  margin-top: 5rem;
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(1rem);
  -webkit-backdrop-filter: blur(1rem);
  border: 1px solid rgba(255, 255, 255, 0.8);
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .formulario {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.formulario__grupo {
  margin-bottom: 2rem;
}
.formulario__grupo:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .formulario__grupo {
    margin-bottom: 0;
  }
}
.formulario__label {
  display: inline-block;
  font-size: 2.2rem;
  margin-left: 1rem;
  cursor: pointer;
}
.formulario__grupo-input {
  position: relative;
}
.formulario__input {
  margin-top: 1rem;
  width: 100%;
  padding: 1.8rem;
  outline: none;
  border: 0.3rem solid transparent;
  border-radius: 0.8rem;
  transition: 0.3s ease all;
}
.formulario__input:focus {
  border: 0.3rem solid #0075ff;
  box-shadow: 3px 0px 30px rgba(163, 163, 163, 0.4);
}
.formulario__input-error {
  font-size: 1.4rem;
  margin: 0;
  display: none;
}
.formulario__input-error-activo {
  display: block;
}
.formulario__validacion-estado {
  position: absolute;
  right: 10px;
  top: 30px;
  z-index: 100;
  font-size: 2rem;
  opacity: 0;
}
.formulario__checkbox {
  margin-top: 2rem;
  margin-right: 1rem;
}
.formulario__grupo-terminos, .formulario__mensaje, .formulario__btn-enviar {
  grid-column: span 2;
}
.formulario__mensaje {
  background-color: #f66060;
  border-radius: 0.3rem;
  padding: 1rem;
  text-align: center;
  display: none;
}
.formulario__mensaje-activo {
  display: block;
}
.formulario__mensaje-p {
  margin: 0;
  color: #fff;
}
.formulario__btn-enviar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.formulario__submit {
  all: unset;
  padding: 2rem 10rem;
  background-color: #000;
  color: #fff;
  border: 2px solid transparent;
  border-radius: 0.8rem;
  font-size: 2rem;
  font-weight: 700;
  margin: 1rem auto 1rem auto;
  display: block;
  cursor: pointer;
  transition: 0.3s ease all;
}
.formulario__submit:hover {
  background-color: #fff;
  color: #000;
  border: 2px solid #000;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.formulario__mensaje-exito {
  font-size: 1.4rem;
  color: #fff;
  background-color: #119200;
  padding: 1rem 3rem;
  margin: 0;
  display: none;
}
.formulario__mensaje-exito-activo {
  display: block;
}

.formulario__grupo-correcto .formulario__validacion-estado {
  color: #1ed12d;
  opacity: 1;
}
.formulario__grupo-correcto .formulario__input {
  border: 0.3rem solid #1ed12d;
}
.formulario__grupo-incorrecto .formulario__label, .formulario__grupo-incorrecto .formulario__validacion-estado {
  color: #bb2929;
  opacity: 1;
}
.formulario__grupo-incorrecto .formulario__input {
  border: 0.3rem solid #bb2929;
}

.sk-chase {
  width: 40px;
  height: 40px;
  position: relative;
  animation: sk-chase 2.5s infinite linear both;
  display: none;
}

.sk-chase-activo {
  display: block;
}

.sk-chase-dot {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  animation: sk-chase-dot 2s infinite ease-in-out both;
}

.sk-chase-dot:before {
  content: "";
  display: block;
  width: 25%;
  height: 25%;
  background-color: #fff;
  border-radius: 100%;
  animation: sk-chase-dot-before 2s infinite ease-in-out both;
}

.sk-chase-dot:nth-child(1) {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2) {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3) {
  animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4) {
  animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5) {
  animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6) {
  animation-delay: -0.6s;
}

.sk-chase-dot:nth-child(1):before {
  animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2):before {
  animation-delay: -1s;
}

.sk-chase-dot:nth-child(3):before {
  animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4):before {
  animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5):before {
  animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6):before {
  animation-delay: -0.6s;
}

@keyframes sk-chase {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot {
  80%, 100% {
    transform: rotate(360deg);
  }
}
@keyframes sk-chase-dot-before {
  50% {
    transform: scale(0.4);
  }
  100%, 0% {
    transform: scale(1);
  }
}
.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #5e311e;
}

.margin-negativo-10 {
  margin-top: -10rem;
}

.bg-white {
  background-color: #fff;
}

.padding-bottom-0 {
  padding-bottom: 0;
}